.city-score-table {
	border-collapse: collapse;
	font-size: .85em;
	margin: 0 auto 2em;
    width: 100%;
}
.city-score-table .city-score-table-number {
	text-align: right;
}
.city-score-table .city-score-modifier {
  display: inline-block;
  vertical-align: text-bottom;
}
.city-score-table .city-score-modifier:before {
	content:'(';
	margin-left:.5em;
}
.city-score-table .city-score-modifier:after {
	content:')';
}
.city-score-table th,
.city-score-table td {
	padding: .5em .25em;
    vertical-align: baseline;
}

.city-score-table th {
	font-weight: bold;
	text-align: left;
	vertical-align: bottom;
}

.city-score-table td {
	border-top: 1px solid #ccc;
}

.city-score-table table {
    table-layout: fixed;
    width: 100%;
}

.city-score-table table td {
	border-top: none;
	padding: 0;
}

.city-score-table .city-score-legend {
	font-size: 1em;
	margin: 0;
	padding: 0;	
    text-align: left;
	white-space: nowrap;
    width: 6em;
}