.modal-wrapper {
  background-color: rgba(0,0,0, .8);
  bottom: 0;
  display: block;
  left: 0;
  padding: 1em;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 999;
}

.modal {
	background-color: #fff;
	border-radius: 4px;
	height: 100%;
	margin: 0 auto;
	max-width: 600px;
	overflow-y: auto;
	padding: 0;
	position: relative;
}

.modal-content {
	padding: 1em;
}

.close-modal {
  height: 4em;
  overflow: hidden;
  padding: 0;
  position: absolute;
  right: 0;
  text-align: left;
  text-indent: -999em;
  top: 0;
  width: 4em;
  z-index: 1;
}

.close-modal:before,
.close-modal:after {
  border-top: 2px solid #666;
  content: '';
  display: block;
  position: absolute;
  left: 50%;
  margin: -1px 0 0 -1em;
  top: 50%;
  width: 2em;
}

.close-modal:before {
	transform: rotate(45deg);
}

.close-modal:after {
	transform: rotate(-45deg);
}

.modal-title {
  padding: 1em 0 1em;
}
